* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: url("../public/wallpaperflare.com_wallpaper\ \(1\).jpg");
  background-size: cover;
  background-repeat: no-repeat;
}
.App {
  display: flex;
  justify-content: center;
}
.container {
  display: flex;
  flex-direction: column;
  width: 500px;
  height: 720px;
  padding-top: 50px;
}
.output {
  background: rgba(247, 247, 247, 0.75);
  width: 100%;
  height: 100%;
  padding: 20px;
  display: flex;
  justify-content: space-around;
  align-items: end;
  flex-direction: column;
  word-wrap: break-word;
  word-break: break-all;
  border: 1.5px solid #000000;
}
.prev {
  font-size: 32px;
  color: rgba(255, 255, 255, 0.75);
}
.current {
  font-size: 38px;
  color: white;
}
.next {
  display: flex;
  flex-direction: column;
  width: 100%;
  flex: flex-end;
}
.button-row button {
  cursor: pointer;
  border: 1px solid rgb(0, 0, 0);

  width: 25%;
  height: 100px;
  background: rgba(255, 255, 255, 0.75);
  color: #000000;
  font-size: 30px;
}
.button-row .two {
  width: 50%;
}
.button-row button:hover,
.button-row button:focus {
  background: rgba(255, 255, 255, 0.9);
}
